@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&family=Open+Sans&family=Poppins:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0%;
  padding: 0%;
  font-family: poppins;
}

.header {
  display: flex;
  align-items: center;
  background-color: #fff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  text-align: center;
  padding: 12px;
  justify-content: space-between;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100;
}

.brand__name {
  margin-right: 15px;
}

.header__center {
  display: flex;
  align-items: center;
  flex: 0.5 1;
  background-color: whitesmoke;
  padding: 5px;
  border-radius: 20px;
}

.header__center > input {
  border: none;
  width: 100%;
  padding: 8px;
  background-color: whitesmoke;
}

.header__center > input:focus {
  outline: none;
}

.header__right {
  display: flex;
  align-items: center;
  margin-right: 20px;
  justify-content: space-around;
}

.header__right .notif {
  margin-right: 12px;
}

.card {
  width: 340px;
  display: flex;
  justify-content: space-between;
  height: 180px;
  background-color: #fff;
  border-radius: 8px;

  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0),
    0 3px 5px -1px rgba(0, 0, 0, 0);
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.card__head {
  padding: 15px;
  margin-top: 15px;
  color: #404040;
}

.card__content {
  font-size: large;
}

.image__section {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

.image {
  width: 150px;
  margin-top: 25px;
  margin-bottom: 25px;
}

.main__content {
  flex: 0.8 1;
  margin-left: 25px;
  background-color: #f8f8f8;
}

.content__items {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 20px;
  padding: 25px;
}

.card__align {
  display: flex;
  justify-content: space-around;
  margin-bottom: 50px;
}

.charts {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.date__picker {
  margin-top: 50px;
  text-align: center;
  margin-bottom: 50px;
  align-items: center;
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  width: 91%;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  margin: 30px;
}

.date__picker > p {
  font-size: 22px;
}

.TableStoryList {
  width: 90%;
  margin-left: 50px;
  justify-content: center;
}

.TablePublicationList {
  width: 90%;
  margin-left: 50px;
  justify-content: center;
}

.pieChart {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.pieChart > p {
  font-size: 20px;
  margin-top: 8px;
  font-weight: bold;
}

.bar__graph {
  border-radius: 10px;
  background-color: #fff;
  padding: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.bar__graph > p {
  font-size: 20px;
  margin-top: 8px;
  font-weight: bold;
  background-color: #fff;
}

.Line__chart {
  width: 88%;
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 20px;
  margin-left: 50px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.Line__chart {
  border-radius: 10px;
  font-size: 20px;
  margin-top: 15px;
  font-weight: bold;
  background-color: #fff;
}

.sidebar {
  padding: 12px;
  background-color: white;
}

.sidebar__head {
  font-size: 22px;
  text-align: center;
  margin-top: 28px;
  font-weight: 800;
  font-family: poppins;
}

.sidebar__items {
  display: flex;
  flex-direction: row;
  text-align: center;
  align-items: center;
  font-size: 18px;
  padding: 20px;
}
.sidebar__items > p {
  margin-left: 30px;
}

.sidebar__items:hover {
  background-color: #eeecda;
  cursor: pointer;
}

.sidebar__icon {
  color: #7579e7;
}

.sidebar__items:hover > .sidebar__icon {
  color: #000;
}

.charts {
  margin-top: 50px;
  display: flex;
}

.pie__chart {
  width: 300px;
}
.pieChart > p {
  text-align: center;
}

.main {
  display: flex;
  background-color: #f8f8f8;
}

.sidebar {
  flex: 0.2 1;
}

.content {
  flex: 0.75 1;
}

.link__ {
  text-decoration: none;
  margin-left: 15px;
  color: #000;
}

